:root {
  --grey: #19202c;
  --dark_grey: #30384c80;
  --light_grey: #c2c2c2;
  --light_grey_bg: #ebebeb80;
  --light_white_bg: #e9e9e9;
  --border: #d8d8d880;
  --menuMedia: #898989;
  --bg: white;
  --bg_blurred: #ffffff80;
  --icon: black;
  --menuBurg: white;
  --menuBurgBorder: #1c1c1c;
}

.darkMode {
  --grey: #e1e1e1;
  --dark_grey: #e1e1e1;
  --light_grey: #c2c2c2;
  --light_grey_bg: #ebebeb80;
  --light_white_bg: #e9e9e9;
  --border: #d8d8d880;
  --menuMedia: #e1e1e1;
  --bg: #1c1c1c;
  --bg_blurred: #1c1c1c80;
  --icon: #fff;
  --menuBurg: #1c1c1c;
  --menuBurgBorder: #fff;
}

@media (prefers-color-scheme: dark) {
  :root:not(.darkMode) {
    --grey: #19202c;
    --dark_grey: #30384c80;
    --light_grey: #c2c2c2;
    --light_grey_bg: #ebebeb80;
    --light_white_bg: #e9e9e9;
    --border: #d8d8d880;
    --menuMedia: #898989;
    --bg: white;
    --bg_blurred: #ffffff80;
    --icon: black;
    --menuBurg: white;
    --menuBurgBorder: #1c1c1c;
  }
}

* {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  background-color: #0000;
